.auth-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100vh; /* Ensure full viewport height */
}

.auth-toggle {
  margin-bottom: 1rem;
}

.auth-toggle-btn {
  margin-right: 0.5rem;
  padding: 0.5rem 1rem;
  background-color: #f0f0f0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.auth-toggle-btn.active {
  background-color: #ccc;
}

.auth-form {
  width: 400px;
  padding: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.auth-form h2 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.auth-form label {
  display: block;
  margin-bottom: 0.5rem;
  font-size: 1rem;
}

.auth-form input {
  width: 95%;
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 0.5rem;
}

.auth-form button {
  width: 100%;
  padding: 0.5rem 1rem;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.auth-form p {
  margin-top: 1rem;
}

.auth-form p button {
  background: none;
  border: none;
  color: #007bff;
  cursor: pointer;
}

.header {
  font-size: 35px;
  font-weight: bold;
  margin-bottom: 1rem;
}

.form-container {
  margin-top: 20px;
}
